<template>
  <div
    style="
      width: 100vw;
      height: 100vh;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    "
  >
    <div ref="threeContainer" style="width: 100%; height: 90%"></div>
    <button
      :disabled="isRolling"
      @click="rollDice"
      style="
        margin-top: 20px;
        padding: 10px 20px;
        font-size: 16px;
        background-color: rgba(255, 255, 255, 0.5);
        border: none;
        border-radius: 5px;
        color: #000;
        cursor: pointer;
        opacity: 0.8;
      "
      :style="isRolling ? 'cursor: not-allowed; opacity: 0.5;' : ''"
    >
      Подбросить кубики
    </button>
  </div>
</template>

<script>
import * as THREE from "three";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";

export default {
  name: "TwoDice",
  data() {
    return {
      isRolling: false,
      dice1Velocity: 0,
      dice2Velocity: 0,
      gravity: -0.01,
      bounceFactor: 0.7,
      maxBounces: 3,
      dice1Bounces: 0,
      dice2Bounces: 0,
      dampingFactor: 0.8, // Коэффициент уменьшения скорости при каждом отскоке
    };
  },
  mounted() {
    this.initScene();
  },
  methods: {
    scene: null,
    camera: null,
    renderer: null,
    dice1: null,
    dice2: null,

    initScene() {
      const container = this.$refs.threeContainer;
      this.scene = new THREE.Scene();

      this.camera = new THREE.PerspectiveCamera(
        50,
        container.clientWidth / container.clientHeight,
        0.1,
        1000
      );
      this.camera.position.z = 6;

      this.renderer = new THREE.WebGLRenderer({ antialias: true });
      this.renderer.setSize(container.clientWidth, container.clientHeight);
      this.renderer.setPixelRatio(window.devicePixelRatio);
      container.appendChild(this.renderer.domElement);

      this.createGradientBackground();

      const ambientLight = new THREE.AmbientLight(0xffffff, 0.7);
      this.scene.add(ambientLight);

      const directionalLight1 = new THREE.DirectionalLight(0xffffff, 0.6);
      directionalLight1.position.set(-5, 5, 5);
      this.scene.add(directionalLight1);

      const directionalLight2 = new THREE.DirectionalLight(0xffffff, 0.6);
      directionalLight2.position.set(5, 5, 5);
      this.scene.add(directionalLight2);

      const loader = new GLTFLoader();
      loader.load(
        "https://zaharovich.github.io/kubik/models/Dice_03.glb",
        (gltf) => {
          console.log("Модель успешно загружена!");
          this.dice1 = gltf.scene.clone(true);
          this.dice2 = gltf.scene.clone(true);

          this.positionDice(this.dice1, -1.5);
          this.positionDice(this.dice2, 1.5);

          this.scene.add(this.dice1);
          this.scene.add(this.dice2);

          this.animate();
        },
        undefined,
        (error) => {
          console.error("Ошибка загрузки модели:", error);
        }
      );

      window.addEventListener("resize", this.onWindowResize);
    },

    createGradientBackground() {
      const texture = new THREE.CanvasTexture(this.generateGradient());
      const geometry = new THREE.PlaneGeometry(20, 20);
      const material = new THREE.MeshBasicMaterial({ map: texture });
      const plane = new THREE.Mesh(geometry, material);
      plane.position.z = -10;
      this.scene.add(plane);
    },

    generateGradient() {
      const canvas = document.createElement("canvas");
      canvas.width = 512;
      canvas.height = 512;
      const ctx = canvas.getContext("2d");
      const gradient = ctx.createRadialGradient(
        canvas.width / 2,
        canvas.height / 2,
        50,
        canvas.width / 2,
        canvas.height / 2,
        canvas.width / 2
      );
      gradient.addColorStop(0, "#031706");
      gradient.addColorStop(1, "#000");
      ctx.fillStyle = gradient;
      ctx.fillRect(0, 0, canvas.width, canvas.height);
      return canvas;
    },

    positionDice(dice, xPosition) {
      const box = new THREE.Box3().setFromObject(dice);
      const center = box.getCenter(new THREE.Vector3());
      const size = box.getSize(new THREE.Vector3()).length();
      dice.position.set(xPosition - center.x, -center.y, -center.z);
      const scale = 2 / size;
      dice.scale.set(scale, scale, scale);
      dice.rotation.set(0, 0, 0);
    },

    animate() {
      requestAnimationFrame(() => this.animate());
      this.renderer.render(this.scene, this.camera);
    },

    onWindowResize() {
      const container = this.$refs.threeContainer;
      this.camera.aspect = container.clientWidth / container.clientHeight;
      this.camera.updateProjectionMatrix();
      this.renderer.setSize(container.clientWidth, container.clientHeight);
    },

    rollDice() {
      if (this.isRolling || !this.dice1 || !this.dice2) return;

      this.isRolling = true;

      this.dice1Velocity = 0.15 + Math.random() * 0.05;
      this.dice2Velocity = 0.15 + Math.random() * 0.05;

      this.dice1Bounces = 0;
      this.dice2Bounces = 0;

      const duration = 1800;
      const startTime = performance.now();

      const animateBounce = (time) => {
        const elapsed = time - startTime;

        if (elapsed < duration) {
          this.applyGravityAndBounce(
            this.dice1,
            "dice1Velocity",
            "dice1Bounces"
          );
          this.applyGravityAndBounce(
            this.dice2,
            "dice2Velocity",
            "dice2Bounces"
          );

          // Вращение
          this.dice1.rotation.x += 0.05;
          this.dice1.rotation.y += 0.05;

          this.dice2.rotation.x += 0.06;
          this.dice2.rotation.y += 0.04;

          requestAnimationFrame(animateBounce);
        } else {
          this.isRolling = false;
        }
      };

      requestAnimationFrame(animateBounce);
    },

    applyGravityAndBounce(dice, velocityKey, bounceKey) {
      if (this[bounceKey] >= this.maxBounces) return;

      dice.position.y += this[velocityKey];
      this[velocityKey] += this.gravity;

      if (dice.position.y <= 0) {
        dice.position.y = 0;
        this[velocityKey] *= -this.bounceFactor;
        this[bounceKey]++;

        if (this[bounceKey] >= this.maxBounces) {
          this[velocityKey] *= this.dampingFactor; // Постепенное уменьшение скорости
          if (Math.abs(this[velocityKey]) < 0.01) {
            this[velocityKey] = 0; // Полная остановка
          }
        }
      }
    },
  },
  beforeUnmount() {
    window.removeEventListener("resize", this.onWindowResize);
  },
};
</script>

<style>
body {
  margin: 0;
  overflow: hidden;
}
button {
  cursor: pointer;
}
</style>
